const FOO = 'hello, foo!';


console.log(FOO);

class Cat {
  constructor(name) {
    this.name = name;
  }
  set name(name) {
    this._name = name;
  }
  get name() {
    return this._name;
  }
  walk() {
    return this._name + 'が歩いてます';
  }
}

/* Catクラスのインスタンス作成 */

var cat1 = new Cat('タマ');
var cat2 = new Cat('コタロー');
console.log(cat1.walk()); // タマが歩いています
console.log(cat2.walk());




